<template>
  <div class="w-full block sm:flex">
    <div class="w-full sm:w-1/2 px-6 sm:px-0">
        <div class="search w-full sm:w-1/2">
          <div class="pt-2 relative mx-auto text-gray-600">
              <button type="submit" class="absolute left-0 top-0 mt-5 mx-4">
              <svg class="text-gray-600 h-4 w-4 fill-current" xmlns="http://www.w3.org/2000/svg"
                  xmlns:xlink="http://www.w3.org/1999/xlink" version="1.1" id="Capa_1" x="0px" y="0px"
                  viewBox="0 0 56.966 56.966" style="enable-background:new 0 0 56.966 56.966;" xml:space="preserve"
                  width="512px" height="512px">
                  <path
                  d="M55.146,51.887L41.588,37.786c3.486-4.144,5.396-9.358,5.396-14.786c0-12.682-10.318-23-23-23s-23,10.318-23,23  s10.318,23,23,23c4.761,0,9.298-1.436,13.177-4.162l13.661,14.208c0.571,0.593,1.339,0.92,2.162,0.92  c0.779,0,1.518-0.297,2.079-0.837C56.255,54.982,56.293,53.08,55.146,51.887z M23.984,6c9.374,0,17,7.626,17,17s-7.626,17-17,17  s-17-7.626-17-17S14.61,6,23.984,6z" />
              </svg>
              </button>
              <input v-model="search" class="bg-white h-10 pl-10 pr-3 w-full rounded-lg text-sm focus:outline-none"
              type="search" name="search" placeholder="Find customer">
          </div>
        </div>
    </div>
    <div class="w-full sm:w-1/2 px-6 sm:px-0 flex justify-end mt-6 sm:mt-0">
        <div class="search w-2/3 sm:w-1/3 pt-2">
          <button @click="toggleFilter = !toggleFilter" class="bg-white text-sm py-2 px-4 rounded w-full flex">
            <span class="w-4/5 text-left">Filter by {{ filter }}</span>
            <div class="w-1/5 flex justify-end">
              <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                <path fill-rule="evenodd" d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" clip-rule="evenodd" />
              </svg>
            </div>
          </button>
          <button v-if="toggleFilter" @click="toggleFilter = false" class="absolute top-0 right-0 bottom-0 left-0 h-full w-full"></button>
          <div v-if="toggleFilter" class="absolute w-48 bg-white rounded-lg text-sm p-3 mt-1 shadow-sm z-10">
            <a @click="sortData('Name (A-Z)')" href="#" class="block text-left px-4 py-3 hover:bg-menu-hover">Name (A-Z)</a>
            <a @click="sortData('Name (Z-A)')" href="#" class="block text-left px-4 py-3 hover:bg-menu-hover">Name (Z-A)</a>
          </div>
        </div>
    </div>
  </div>
  <div class="mt-16 grid sm:grid-cols-4 grid-cols-1 gap-x-8 gap-y-12 px-6 sm:px-0">
    <div v-if="filterResult.length === 0" class="text-red-500">
      No customer(s) found
    </div>
    <div v-else v-for="item in filterResult" :key="item.id" class="bg-white customer_card shadow-sm rounded-lg">
      <div class="image relative">
        <img class="absolute right-0 sm:relative h-28 w-28 sm:h-full sm:w-full p-6 sm:p-0" :src="require(`@/assets/img/${item.img}`)" />
      </div>
      <div class="p-6 text-left">
         <div class="text-lg sm:text-2xl font-semibold">{{ item.name }}</div>
         <div class="text-gray-400">@{{item.username}}</div>
         <div class="mt-4 text-phrase">"{{ item.company.catchPhrase }}"</div>
         <div class="mt-4">
           <div class="flex">
             <div>
                <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-icons" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                 <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
                </svg>
             </div>
             <div class="pl-4">{{ item.email }}</div>
           </div>
           <div class="flex mt-3">
             <div>
                <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-icons" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
                </svg>
             </div>
             <div class="pl-4">
                <div>{{ item.address.street }}, {{item.address.suite }}</div>
                <div>{{ item.address.city }}, {{item.address.zipcode}},</div>
                <div>{{ item.address.geo.lat }}, {{ item.address.geo.lng }}</div>
             </div>
           </div>
           <div class="flex mt-3">
             <div>
               <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-icons" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M3 5a2 2 0 012-2h3.28a1 1 0 01.948.684l1.498 4.493a1 1 0 01-.502 1.21l-2.257 1.13a11.042 11.042 0 005.516 5.516l1.13-2.257a1 1 0 011.21-.502l4.493 1.498a1 1 0 01.684.949V19a2 2 0 01-2 2h-1C9.716 21 3 14.284 3 6V5z" />
                </svg>
             </div>
             <div class="pl-4">
                {{ item.phone }}
             </div>
           </div>
           <div class="flex mt-3">
             <div>
              <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-icons" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M21 12a9 9 0 01-9 9m9-9a9 9 0 00-9-9m9 9H3m9 9a9 9 0 01-9-9m9 9c1.657 0 3-4.03 3-9s-1.343-9-3-9m0 18c-1.657 0-3-4.03-3-9s1.343-9 3-9m-9 9a9 9 0 019-9" />
              </svg>
             </div>
             <div class="pl-4">
                {{ item.website }}
             </div>
           </div>
           <div class="flex mt-3">
             <div>
              <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-icons" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M21 13.255A23.931 23.931 0 0112 15c-3.183 0-6.22-.62-9-1.745M16 6V4a2 2 0 00-2-2h-4a2 2 0 00-2 2v2m4 6h.01M5 20h14a2 2 0 002-2V8a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
              </svg>
             </div>
             <div class="pl-4">
                {{ item.company.name }}
             </div>
           </div>
           <div class="flex mt-3">
             <div>
              <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-icons" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 21V5a2 2 0 00-2-2H7a2 2 0 00-2 2v16m14 0h2m-2 0h-5m-9 0H3m2 0h5M9 7h1m-1 4h1m4-4h1m-1 4h1m-5 10v-5a1 1 0 011-1h2a1 1 0 011 1v5m-4 0h4" />
              </svg>
             </div>
             <div class="pl-4">
                {{ item.company.bs }}
             </div>
           </div>
         </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      toggleFilter: false,
      filter: 'Name (A-Z)',
      customers: [],
      search: '',
    }
  },
  computed: {
    customerData() {
      this.customers.forEach((element, i) => {
        element.img = i + '.png'
      })
      return this.customers
    },
    filterResult() {
      return this.customerData.filter(d => {
        if(this.search !== ''){
          return d.name.toLowerCase().includes(this.search.toLowerCase())
        }else{
          return this.customerData
        }
      })
    }
  },
  methods: {
    sortData(data) {
      this.filter = data
      this.toggleFilter = false

      function compare(a, b) {
        if(data === 'Name (A-Z)'){
          if (a.name < b.name)
            return -1
          if (a.name > b.name)
            return 1
          return 0
        }else {
          if (a.name > b.name)
            return -1
          if (a.name < b.name)
            return 1
          return 0
        }
      }
      this.customerData = this.customerData.sort(compare)
    }
  },
  async created() {
    const response = await fetch("http://jsonplaceholder.typicode.com/users");
    const data = await response.json();
    this.customers = data
  }
}
</script>

<style scoped>
.customer_card {
  max-width: 363px;
}
</style>